:root {
  --main-color: #999;
}

@import './style/components/topbar-back.css';
@import './style/components/bottom-nav.css';

* {
  box-sizing: border-box;
}
h1,h2,h3,h4,h5,h6 {
  margin: 0;
}
body {
  margin: 0;
  padding: 0;
  /* font-family: 'Exo', sans-serif; */
  /* font-weight: 100; */
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--main-color);
}
#root {
  /* margin-bottom: 60px; */
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.holster {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-flow: column nowrap;
  font-family: monospace;
}

.container {
  display: flex;
  overflow-y: hidden;
  outline: 1px dashed lightgray;
  flex: none;
}
.container.x {
  width: 100%;
  height: auto;
  flex-flow: row nowrap;
}
.x.mandatory-scroll-snapping {
  scroll-snap-type: x mandatory;
}
.container > div {
  /* text-align: center; */
  scroll-snap-align: center;
  flex: none;
}
.x.container > div {
  overflow: hidden;
  position: relative;
  margin: 0 10px;
  border: 1px solid black;
  border-radius: 20px;
  width: 60%;
  height: 170px;
  /*background: url(https://assets.afcdn.com/recipe/20180927/82371_w420h344c1cx1355cy1305cxt0cyt0cxb3861cyb2572.jpg);*/
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.x.container > div > .infos {
  position: absolute;
  bottom: 0;
  width: 100%;
  background: white;
}
.x.container > div > .infos > * {
  margin: 0;
  padding: 0;
}
.x.container.top > div {
  overflow: hidden;
  position: relative;
  margin: 0 10px;
  border: 1px solid black;
  border-radius: 20px;
  height: 155px;
  width: 40%;
  background: url(https://assets.afcdn.com/recipe/20180927/82371_w420h344c1cx1355cy1305cxt0cyt0cxb3861cyb2572.jpg) no-repeat cover center center;
}


.my-facebook-button-class {
  border: 1px solid black;
}