.topbar-back {
  position: fixed;
  width: 100%;
  height: 56px;
  top: 0;
  left: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  border: 1px solid black;
}
.topbar-back button {
  background: none;
  border: none;
}
.bottom-nav {
  position: fixed;
  bottom: 0;
  border: 1px solid black;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  background: #ffffff;
}

nav.bottom-nav > div > a {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

:root {
  --main-color: #999;
}

* {
  box-sizing: border-box;
}
h1,h2,h3,h4,h5,h6 {
  margin: 0;
}
body {
  margin: 0;
  padding: 0;
  /* font-family: 'Exo', sans-serif; */
  /* font-weight: 100; */
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #999;
  color: var(--main-color);
}
#root {
  /* margin-bottom: 60px; */
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.holster {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  font-family: monospace;
}

.container {
  display: -webkit-flex;
  display: flex;
  overflow-y: hidden;
  outline: 1px dashed lightgray;
  -webkit-flex: none;
          flex: none;
}
.container.x {
  width: 100%;
  height: auto;
  -webkit-flex-flow: row nowrap;
          flex-flow: row nowrap;
}
.x.mandatory-scroll-snapping {
  -webkit-scroll-snap-type: x mandatory;
      -ms-scroll-snap-type: x mandatory;
          scroll-snap-type: x mandatory;
}
.container > div {
  /* text-align: center; */
  scroll-snap-align: center;
  -webkit-flex: none;
          flex: none;
}
.x.container > div {
  overflow: hidden;
  position: relative;
  margin: 0 10px;
  border: 1px solid black;
  border-radius: 20px;
  width: 60%;
  height: 170px;
  /*background: url(https://assets.afcdn.com/recipe/20180927/82371_w420h344c1cx1355cy1305cxt0cyt0cxb3861cyb2572.jpg);*/
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.x.container > div > .infos {
  position: absolute;
  bottom: 0;
  width: 100%;
  background: white;
}
.x.container > div > .infos > * {
  margin: 0;
  padding: 0;
}
.x.container.top > div {
  overflow: hidden;
  position: relative;
  margin: 0 10px;
  border: 1px solid black;
  border-radius: 20px;
  height: 155px;
  width: 40%;
  background: url(https://assets.afcdn.com/recipe/20180927/82371_w420h344c1cx1355cy1305cxt0cyt0cxb3861cyb2572.jpg) no-repeat cover center center;
}


.my-facebook-button-class {
  border: 1px solid black;
}
